import React from "react";
import { connect } from "react-redux";
import { Button, Container, Divider, Form, Grid, Segment } from 'semantic-ui-react'
import logo from '../../assets/logo.png'
import history from "../../history";

import {login} from "../../actions/LoginActions";
// import {toastr} from 'react-redux-toastr'

class Login extends React.Component {
    state = {
        username: '',
        password: '',
        hasError: false,
        isPendingToRecieveData:false
    }
    inputOnChange = (values) => { 
        this.setState({hasError:false})
        this.setState({
            [values.target.id]:values.target.value
        })
    }
    onSubmit = () => {
        // console.log(this.state);

        this.setState({ isPendingToRecieveData: true });
        this.props.login(this.state.username, this.state.password).then(()=>{
            this.setState({ isPendingToRecieveData: false });
            history.push('/');
            // toastr.success('  ','    ')
        }).catch(() => {
            this.setState({isPendingToRecieveData: false, hasError:true})
            // toastr.error('','      ')
        });
    };

    render() {
        return (
        <Container id="login" className="rtl">
           <Segment placeholder>
                    <Grid columns={2} relaxed='very' stackable>

                <Grid.Column className="ltr text-center only-device" verticalAlign='middle'>
                    <img src={logo} height="230"/>
                </Grid.Column>
                <Grid.Column>
                    <Form>
                        <Form.Input
                        error={this.state.hasError}
                        className="ltr placeholder-rtl"
                        id="username"
                        onChange={this.inputOnChange}
                        icon='user'
                        iconPosition='left'
                        label=' '
                        placeholder=' '
                        />
                    <Form.Input
                        error={this.state.hasError}
                        className="ltr placeholder-rtl"
                        id="password"
                        onChange={this.inputOnChange}
                        icon='lock'
                        iconPosition='left'
                        label=''
                        type='password'
                        placeholder=''            
                    />

                    <Button loading={this.state.isPendingToRecieveData} onClick={this.onSubmit} className="yekan" content='  ' primary />
                    </Form>
                </Grid.Column>

                        <Grid.Column className="ltr text-center only-desktop" verticalAlign='middle'>
                            <img src={logo} height="230"/>
                </Grid.Column>
                </Grid>

                <Divider vertical></Divider>
            </Segment>
        </Container>
        );
    }
}

export default connect(
    null,
    {login}
)(Login);
