import React from "react";
import { Form, Label, Header } from "semantic-ui-react";
import renderField from "./RenderField";
const BuyerDetails = props => {
  return (
    <React.Fragment>
      <Header as="h3" textAlign="right">
        <span>: </span>
      </Header>
      <Form.Group className="rtl" widths="equal">
        {renderField(props.buyer, "first_name", "")}
        {renderField(props.buyer, "last_name", " ")}
        {renderField(props.buyer, "phone_number", "  ")}
      </Form.Group>
      <Form.Group widths="equal">
        {renderField(props.buyer, "email", "")}
        {renderField(props.buyer, "address", "")}
        {renderField(props.buyer, "points", " ")}
      </Form.Group>
    </React.Fragment>
  );
};
export default BuyerDetails;
