import React from "react";
import { Form, Label, Header } from "semantic-ui-react";
import { enToFa, priceToPersian } from "../utils/numberUtils";
import { standardTimeToJalaali } from "../utils/jalaaliUtils";
import renderField from "./RenderField";
import { compose } from "../../utils/FunctionalUtils";

const BillDetails = props => {
  return (
    <div>
      <Header as="h3" textAlign="right">
        <span>: </span>
      </Header>
      <Form.Group className="rtl" widths="equal">
        {renderField(
          props.bill,
          "create_date",
          " ",
          compose(enToFa, standardTimeToJalaali)
        )}
        {renderField(props.bill, "total_discount", " ", text =>
          priceToPersian(text)
        )}
        {renderField(props.bill, "used_points", "  ", text =>
          enToFa(text)
        )}
      </Form.Group>
      <Form.Group className="rtl" widths="equal">
        {renderField(props.bill, "total_discount", " ", text =>
          priceToPersian(text)
        )}
        {renderField(props.bill, "price", "  ", text =>
          priceToPersian(text)
        )}
        {renderField(props.bill, "final_price", " ", text =>
          priceToPersian(text)
        )}
      </Form.Group>
    </div>
  );
};
export default BillDetails;
