import React from "react";
import { Form, Label, Header } from "semantic-ui-react";
import renderField from "./RenderField";

const SellerDetails = props => {
  return (
    <React.Fragment>
      <Header as="h3" textAlign="right">
        <span>: </span>
      </Header>
      <Form.Group className="rtl" widths="equal">
        {renderField(props.seller, "first_name", "")}
        {renderField(props.seller, "last_name", " ")}
      </Form.Group>
      <Form.Group widths="equal">
        {renderField(props.seller, "username", " ")}
        {renderField(props.seller, "national_id", " ")}
      </Form.Group>
    </React.Fragment>
  );
};
export default SellerDetails;
