import React from "react";
import { Form, Popup, Button, Label, Divider, Card } from "semantic-ui-react";
import renderField from "./RenderField";
import { priceToPersian, enToFa } from "../utils/numberUtils";
import { useToggle } from "../../utils/Hooks";

const RenderItem = props => {
  const [productInfoIsOpen, toggleProductInfoIsOpen] = useToggle(false);
  return (
    <React.Fragment>
      <Form.Group
        className="rtl"
        widths="4"
        key={`bill_productItem_${props.index}`}
      >
        {/* {renderField(props.productItem, "pk", " ", enToFa)} */}
        <Form.Field>
          <Popup
            content={
              <Product
                product={props.productItem.product}
                onClose={toggleProductInfoIsOpen}
              />
            }
            style={{ top: -100 }}
            open={productInfoIsOpen}
            className="no-filter"
            position="bottom center"
            wide="very"
            trigger={
              <Label
                onClick={toggleProductInfoIsOpen}
                color="blue"
                icon="info"
                as="a"
              >
                 
              </Label>
            }
          />
        </Form.Field>
        {renderField(
          props.productItem.product,
          "selling_price",
          "  ",
          priceToPersian
        )}
        {renderField(props.productItem, "amount", "", enToFa)}
      </Form.Group>
      <Form.Group widths="4">
        {renderField(props.productItem, "end_of_roll", " ", roll => {
          return roll
            ? ` (${enToFa(props.productItem.end_of_roll_amount)} )`
            : "";
        })}
        {renderField(
          props.productItem,
          "discount",
          " ",
          priceToPersian
        )}
        {renderField(props.productItem, "price", "", priceToPersian)}
        {renderField(
          props.productItem,
          "final_price",
          " ",
          priceToPersian
        )}
      </Form.Group>
      <Divider />
    </React.Fragment>
  );
};

const Product = props => {
  return (
    <Card className="rtl">
      <Card.Content>
        <Card.Header className="yekan"> </Card.Header>
      </Card.Content>
      <Card.Content extra>
        <Form>
          {renderField(props.product, "code", " ", enToFa)}
          {renderField(props.product, "name", "")}
          {renderField(props.product, "branch", " ", enToFa)}
          {renderField(
            props.product,
            "background_color",
            "   ",
            enToFa
          )}
          {renderField(props.product, "design_color", "  ", enToFa)}
          {renderField(props.product, "material", " ", enToFa)}
          {renderField(props.product, "f_type", "  ", enToFa)}
          {renderField(props.product, "design", "  ", enToFa)}
          {renderField(
            props.product,
            "selling_price",
            "  ",
            priceToPersian
          )}
          {renderField(
            props.product,
            "buying_price",
            " ",
            priceToPersian
          )}
          {renderField(props.product, "stock_amount", " ", enToFa)}
          <div className="text-center">
            <Button.Group className="ltr">
              <Button className="yekan" color="black" onClick={props.onClose}>
                
              </Button>
            </Button.Group>
          </div>
        </Form>
      </Card.Content>
    </Card>
  );
};

export default RenderItem;
